                        { name: '小市街道', vle: '年关大促' },
                        { name: '罗汉镇', vle: '年关大促' },
                    ],
                }
            ],
            brandArr: [
                { img: img0, title: '蜜桔新鲜水果10斤', url: '' },
                { img: img1, title: '紫薯新鲜番薯地瓜', url: '' },
                { img: img2, title: '红富士苹果大果5斤', url: '' },
            ],
            recommendedArr: [
                { img: img3, title: '蜜桔新鲜水果10斤', url: '', price: '33.9', prePrice: '16.9' },
                { img: img4, title: '紫薯新鲜番薯地瓜', url: '', price: '33.9', prePrice: '16.9' },
                { img: img5, title: '红苹果', url: '', price: '33.9', prePrice: '16.9' },
            ],
        }
    },
    created() {},
    methods: {
        // 跳街道
        goStreet(title, val) {
            try {
                this.$Api.callApp('street', JSON.stringify({ area: title, street: val.name }));
            } catch (err) {
                console.log(err);
                this.$router.push({ path: '/street', query: { area: title, street: val.name } })
            }
        },
        // 本地生活跳转
        goLocal() {
            // 调用安卓原生方法
            try {
                this.$Api.callApp('local-life');
            } catch(err) {
                console.log(err);
                this.$router.push('/local-life');
            }
        },
        more() {
            // 调用安卓原生方法
            try {
                this.$Api.callApp('more');
            } catch(err) {
                console.log(err);
                // this.$router.push('/mall-details');
            }
        },
        // 跳转专区列表
        goClick(v, i) {
            try {
                this.$Api.callApp('zone-list', JSON.stringify(i));
            } catch(err) {
                console.log(err, i);
                this.$router.push({ path: '/zone-list', query: { data: v } })
            }
            
        },
        goPush() {
            // 调用安卓原生方法
            try {
                this.$Api.callApp('mall-details');
            } catch(err) {
                console.log(err);
                this.$router.push('/mall-details');
            }
        },
    }
}
</script>

