    name: 'Mall',
    data() {
        return {
            arr: this.$route.query.data.arr,
            data: this.$route.query.data,
            timeArr: [
                { img: img700, title: '米脂小米 2021年新米农家吃的黄小米 杂粮粥 粮食小米粥 陕西 陕北特产油小米超值 五斤装', sold: 3225, price: 998, address: '南城街道' },
                { img: img701, title: '新鲜白萝卜五斤带箱农家自种大个萝卜时令蔬菜脆甜生吃长萝卜', sold: 3225, price: 56.7, address: '北城街道' },
                { img: img702, title: '膳太 农家无芯 白莲子 250g无硫去心莲子新鲜糯粉干货 一煮就酥香', sold: 3225, price: 35, address: '南城街道' },
                { img: img703, title: '土豆1500g 烧烤火锅食材 产地直供新鲜蔬菜干净芋头定西马铃薯紫洋芋宝宝辅食 定西土豆2500g', sold: 3225, price: 3.4, address: '南城街道' },
                { img: img704, title: '现摘顺丰发  生吃沙瓤西红柿农家自种小番茄时令生鲜 自然熟健康轻食水果 5斤装【净重4.5-5斤】', sold: 3225, price: 24.5, address: '南城街道' },
            ],
        }
    },
    created() {
        try {
            this.data = JSON.parse(this.$route.query.data)
            this.arr = this.data.arr;
        } catch(err) {
            console.log(err);
        }
    },
    methods: {
        //跳转街道
        goClick(name) {
            try {
                this.$Api.callApp('street', JSON.stringify({ area: this.data.title, street: name }));
            } catch(err) {
                console.log(err);
                this.$router.push({ path: '/street', query: { area: this.data.title, street: name } })
            }
        }
    }
}
</script>

