            ],
        }
    },
    created() {},
    methods: {
        goClick(v, i) {
            console.log(v);
            try {
                this.$Api.callApp('zone-list', JSON.stringify(i));
            } catch (err) {
                console.log(err);
                this.$router.push({ path: '/zone-list', query: { data: v } });
            }
        },
        goStreet(title, val) {
            try {
                this.$Api.callApp('street', JSON.stringify({ area: title, street: val.name }));
            } catch (err) {
                console.log(err);
                this.$router.push({ path: '/street', query: { area: title, street: val.name } })
            }
        }
    }
}
</script>

