<script>
import mallimg1 from '@/assets/img/mall/mall-img.png';
import img801 from '@/assets/img/mall/img801.png';
import img802 from '@/assets/img/mall/img802.png';
import img803 from '@/assets/img/mall/img803.png';
import detimg1 from '@/assets/img/mall/det-img1.png';
import detimg2 from '@/assets/img/mall/det-img2.png';
import detimg3 from '@/assets/img/mall/det-img3.png';
import avatar from '@/assets/img/avatar/avatar.png';
import img0 from '@/assets/img/mall/img0.png';
import img1 from '@/assets/img/mall/img1.png';
import img2 from '@/assets/img/mall/img2.png';

import { ImagePreview } from 'vant';

export default {
    name: 'Details',
    data() {
        return {
            opacityStr: 0,
            active: 0,
            imgArr: [
                { img: mallimg1, url: '' },
                { img: mallimg1, url: '' },
                { img: mallimg1, url: '' },
            ],
            scrollTop: 0,
            qjTop: 0,
            xqTop: 0,
            tjTop: 0,
            avatar,
            detimg1,
            detimg2,
            detimg3,
            evaluationArr: [
                { img: avatar, name: '林**强', test:'第一次吃这个桔子，水分足又甜，也比以前买的桔子都好吃，一分价钱一分货，果然是这样，看到青青的以为会酸，果然，甜占9分酸占1分，好吃。', imgArr: [] },
                { img: avatar, name: '一**晶', test:'果肉很脆，很甜，而且个头大个可以放心吃。', imgArr: [img801, img802, img803] },
            ],
            brandArr: [
                { img: img0, title: '蜜桔新鲜水果10斤', url: '' },
                { img: img1, title: '紫薯新鲜番薯地瓜', url: '' },
                { img: img2, title: '红富士苹果大果5斤', url: '' },
                { img: img0, title: '蜜桔新鲜水果10斤', url: '' },
                { img: img1, title: '紫薯新鲜番薯地瓜', url: '' },
                { img: img2, title: '红富士苹果大果5斤', url: '' },
            ],
        }
    },
    created() {

    },
    methods: {
        handleScroll () {
            this.scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            this.opacityStr = this.scrollTop * 2.5 / 1000;

            if(this.scrollTop <= 200) {
                this.active = 0;
            } else if(this.scrollTop <= this.qjTop) {
                this.active = 1;
            } else if(this.scrollTop <= this.xqTop) {
                this.active = 2;
            } else if(this.scrollTop <= this.tjTop) {
                this.active = 3;
            }
        },
        // 滚动条回到顶部
        backTop () {
            if (this.scrollTop > 10) {
                document.documentElement.scrollTop = 0
            }
        },
        tabsClick(obj) {
            if(obj == 0) {
                this.goAnchor(this.$refs.myswipe);
            } else if(obj == 1) {
                this.goAnchor(this.$refs.pj);
            } else if(obj == 2) {
                this.goAnchor(this.$refs.xq);
            } else if(obj == 3) {
                this.goAnchor(this.$refs.tj);
            }
        },
        // 跳转到固定位置
        goAnchor(obj) {
            obj.scrollIntoView(true, { behavior: 'smooth' });
        },
        backClickBtn() {
            try {
                this.$Api.callApp('backMall');
            } catch(err) {
                console.log(err);
                this.$router.go(-1);
            }
        },
        moreClick() {
            console.log('更多');
        },
        imgClick(arr, i) {
            ImagePreview({
                images: arr,
                startPosition: i,
            })
        },
    },
    mounted () {
        this.qjTop = this.$refs.pj.offsetTop + 250;
        this.xqTop = this.$refs.xq.offsetTop + 250;
        this.tjTop = this.$refs.tj.offsetTop + 250;
        window.addEventListener('scroll', this.handleScroll, true)
    },
    destroyed () {
        // 离开该页面需要移除这个监听的事件，不然会报错
        window.removeEventListener('scroll', this.handleScroll)
    }
}
</script>
