import Vue from 'vue'
import App from './App.vue'

import 'normalize.css';
import '@/style/common.less';

import { Lazyload, Popup, Picker, Cell, CellGroup, Uploader, Field, Divider, Toast, Swipe, SwipeItem, CountDown, Tab, Tabs, GoodsAction, GoodsActionIcon, GoodsActionButton, RadioGroup, Radio, Checkbox, CheckboxGroup } from 'vant';
import 'vant/lib/index.css';

import router from './router';

import { Api } from '../src/api/index';

Vue.use(Toast).use(Lazyload).use(Popup).use(Picker).use(Cell).use(CellGroup).use(Uploader).use(Field).use(Divider).use(GoodsAction).use(GoodsActionIcon).use(GoodsActionButton).use(Swipe).use(SwipeItem).use(Checkbox).use(CheckboxGroup).use(CountDown).use(RadioGroup).use(Radio).use(Tab).use(Tabs);

Vue.config.productionTip = false
Vue.prototype.$Api = Api;
Vue.prototype.$Toast = Toast;

new Vue({
    render: h => h(App),
    router,
}).$mount('#app')
